}

.icon-mobile-profile:before {
  content: "\e905";
}

.icon-mobile-catalog:before {
  content: "\e906";
}

.icon-rub:before {
  content: "\f159";
}

.icon-arow-right:before {
  content: "\e901";
}

.icon-arow-left:before {
