.greed-laout {
	grid-template-columns:
		1fr
		minmax(25rem, min-content)
		minmax(20rem, min-content)
		minmax(8rem, min-content)
		50px;

	@include _1440 {
		grid-template-columns:
			1fr
			minmax(20rem, min-content)
			minmax(18rem, min-content)
			minmax(8rem, min-content)
			50px;
	}
	@include _690 {
		grid-template-columns:
			minmax(20rem, min-content)
			minmax(13rem, min-content)
			minmax(7rem, min-content)
			30px;
	}
	@include _490 {
		grid-template-columns:
			minmax(18rem, min-content)
			minmax(7rem, min-content)
			minmax(5rem, min-content)
			30px;
	}
}

.cart-page {
	// .cart-page__title
	&__title {
		color: $color;
		font-size: 3rem;
		margin-bottom: 5rem;
		text-transform: uppercase;
	}

	// .cart-page__body
	&__body {
		gap: 2.5rem;
		display: flex;

		@include _920 {
			flex-direction: column;
		}
	}

	// .cart-page__column
	&__column {
		&:nth-child(n + 1):nth-child(-n + 4) {
			padding: 0 1rem;
		}

		&:nth-child(5) {
			flex: 0 0 50px;
			text-align: center;
		}
	}

	// .cart-page__table-head
	&__table-head {
		display: grid;
		gap: 0.8rem;
		justify-content: space-between;
		align-items: center;
		height: 5rem;
		border: 1px solid $color;
		border-radius: 5px;
		margin-bottom: 2rem;
		font-weight: 700;
		@extend.greed-laout;
		@include _690 {
			display: none;
		}
	}

	// .cart-page__main-content
	&__main-content {
		flex: 1 1 100%;
	}

	// .cart-page__help-order
	&__help-order {
		@include _min-920 {
			flex: 0 0 28.3rem;
		}
	}

	// .cart-page__button
	&__button {
		transition: color 0.3s ease-in-out;
		&:hover {
			color: $color;
		}
	}
}

.help-order {
	@include _920 {
		display: flex;
		gap: 2rem;
	}
	@include _490 {
		flex-direction: column;
	}

	// .help-order__info
	&__info {
		padding: 1.8rem;
		text-align: center;
		border: 1px solid $color;
		border-radius: 10px;
		flex: 0 1 28.3rem;

		@include _min-920 {
			&:not(:last-child) {
				margin-bottom: 3.4rem;
			}
		}

		@include _920 {
			display: flex;
			flex-direction: column;
			flex: 0 1 50%;
		}
	}

	&__title {
		padding-bottom: 2rem;
		font-size: 2.1rem;

		@include _920 {
			display: flex;
			flex-direction: column;
			flex: 1 1 100%;
			height: 100%;
		}
	}

	// .help-order__text
	&__text {
		font-size: 13px;
		margin-bottom: 1.6rem;
		@include _375 {
			font-size: 12px;
		}

		p {
			margin-bottom: 0.5rem;
		}
		span {
			font-weight: 700;
		}
	}

	// .help-order__phone
	&__phone {
		font-size: 2rem;
		font-weight: 500;
	}
}

.order-place {
	// .order-place__send-order
	&__send-order {
		margin-bottom: 2rem;
		padding: 0 0.5rem;
		&._send-order {
			.order-place__send-button {
				display: block;
			}
		}
	}

	// .order-place__footer
	&__footer {
		display: flex;
		gap: 2rem;
		justify-content: space-between;
		@include _490 {
			flex-direction: column-reverse;
		}
	}

	// .order-place__column--login
	&__column--login {
		.login-container {
			position: relative;

			.registrations-modal {
				top: 50px;
				left: 0;
				margin-right: -120px;
				@include _490 {
					width: 100%;
				}
			}

			.recovery-modal {
				top: 50px;
				left: 0;
				@include _490 {
					width: 100%;
				}
			}

			.login-modal {
				top: 50px;
				left: 0;
				@include _490 {
					width: 100%;
				}
			}

			&__body {
				// position: relative;
				// z-index: 9;
				// margin-bottom: 4.3rem;
				// padding: 0 1.5rem;
				// height: 50px;
				// border: 1px solid $color;
				// border-radius: 5px;
				// align-items: center;
				// justify-items: start;
				// grid-template-columns: 30rem 15rem;

				// @include _490 {
				// 	grid-template-columns: 1fr 1fr 1fr;
				// }
			}

			// .login-container__totalprice-order
			// &__totalprice-order {
			// 	font-weight: 600;
			// 	display: flex;
			// 	align-items: center;
			// 	white-space: nowrap;
			// 	.icon-rub {
			// 		align-items: flex-end;
			// 		line-height: 120%;
			// 		margin-left: 0.5rem;
			// 	}
			// }

			// .login-container__discounts
			&__discounts {
				font-weight: 700;
				white-space: nowrap;
			}

			// .login-container__column--result
			&__column--result {
				width: 100%;
				display: flex;
				justify-content: flex-end;
			}
			// .login-container__list
			&__list {
				font-size: 1.5rem;
				width: auto;
			}

			// .login-container__fade-in
			&__fade-in {
				@include _screen-490 {
					display: flex;
					align-items: center;
					justify-content: center;
					padding: 1rem 2rem;
					background: #fff;
					border: 1px solid $color;
					border-radius: 5px;
					height: 50px;
					width: 100%;
					position: absolute;
					opacity: 0;
					z-index: 8;
					top: 0;
					left: 0;
					pointer-events: none;
					box-shadow: 1px 1px 3px #000;
					transition: all 0.5s ease-in-out;

					&._visible {
						pointer-events: all;
						transform: translatey(102%);
						opacity: 1;
						display: flex;
					}
				}
			}

			// .login-container__user-button
			&__user-button {
				pointer-events: none;
				@include _490 {
					pointer-events: all;
				}
			}
		}
	}

	// .order-place__column--button
	&__column--button {
		padding: 0 2rem;
		display: grid;
		grid-template-columns: 1fr;
	}

	// .order-place__checkbox
	&__checkbox {
		padding: 2rem 0;
		display: flex;
		flex-direction: row-reverse;
	}

	// .order-place__info
	&__info {
		max-width: 60rem;
		p {
			font-size: 13px;
		}
	}

	// .order-place__form-button
	&__form-button {
		width: 200px;
		height: 32px;
		color: $bg-color;
		font-size: 1.4rem;
		padding: 0.9rem 3rem;
		border-radius: 16px;
		background: $color;
		justify-self: end;
		@include _425 {
			width: 100%;
		}
	}

	// .order-place__send-button
	&__send-button {
		display: none;
		width: 200px;
		height: 32px;
		color: #fbf9f4;
		font-size: 1.4rem;
		padding: 0.9rem 3rem;
		border-radius: 16px;
		background: #004646;
		justify-self: end;
		@include _425 {
			width: 100%;
		}
	}
}

.shopping-cart {
	// .shopping-cart__item
	&__item {
		display: flex;
		flex-direction: column;
	}

	// .shopping-cart__row
	&__row {
		display: grid;
		gap: 0.8rem;
		justify-content: space-between;
		align-items: center;

		border: 1px solid #d3d3d3;
		border-radius: 5px;
		margin-bottom: 2rem;
		@extend.greed-laout;
		@include _690 {
			row-gap: 4rem;
			padding-bottom: 2rem;
		}
	}
}

.product-cart {
	// .product-cart__column
	&__column {
		font-size: 13px;
		@include _690 {
			font-size: 12px;
		}
		&:nth-child(n + 2):nth-child(-n + 4) {
			padding: 0 1rem;
		}

		&:nth-child(1) {
			@include _690 {
				grid-column: 1 / 5;
			}
		}

		&:nth-child(5) {
			padding: 1.5rem 0;
			text-align: center;
			align-self: start;
			align-self: center;
			@include _690 {
			}
		}

		@include _690 {
			&:nth-child(2) {
				padding-left: 0 !important;
			}
		}
	}

	// .product-cart__product-info
	&__product-info {
		display: flex;
		align-items: center;
	}

	// .product-cart__image
	&__image {
		min-width: 9rem;
		@include _690 {
			min-width: 1rem;
			height: 100%;
		}

		img {
			width: 9rem;
			max-width: 100%;
		}
	}

	// .product-cart__description
	&__description {
		padding: 1rem;
	}

	// .product-cart__units
	&__units {
		white-space: nowrap;
		@include _375 {
			display: block;
		}
	}

	// .product-cart__quantity-price
	&__quantity-price {
		font-weight: 550;
	}

	// .product-cart__button
	&__button {
		transition: color 0.3s ease-in-out;
		&:hover {
			color: $color;
		}
	}
}

.result-container {
	&__body {
		gap: 3rem;
		display: grid;
		justify-content: end;
		align-items: center;
		margin-bottom: 4.3rem;
		padding: 0 1.5rem;
		height: 50px;
		border: 1px solid $color;
		border-radius: 5px;
		align-items: center;
		justify-items: start;
		grid-template-columns: 12rem 15rem;
	}

	&__column {
		font-weight: 700;
		text-align: right;
		&:nth-child(2) {
			justify-self: end;
		}
	}

	&__totalprice-order {
		text-align: right;
		font-weight: 700;
		display: flex;
		align-items: center;
		white-space: nowrap;

		.icon-rub {
			align-items: flex-end;
			line-height: 120%;
			margin-left: 0.5rem;
		}
	}

	// .result-container__discounts
	&__discounts {
	}
}
